.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 12vh;
    background: var(--background-day);
    background-color: #ffabab;
    padding-left: 5%;
    padding-right: 5%;
}

.nav h6 {
    
    font-style: normal;
    font-weight: 800;
    /* width: 6.8%; */
    font-size: 2.6em;
    /* line-height: 34px; */
    letter-spacing: 0.01em;
    color: #101010;
    flex: 0.1;
    /* order: 0; */
    /* flex-grow: 0; */
}

.nav ul{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0.400;
    
    /* padding-left: 5%; */
    /* padding-right: 5%; */
}

.nav ul li{
    
    font-style: normal;
    font-weight: 600;
    font-size: 1.10em;
    line-height: 26px;
    letter-spacing: 0.01em;
    text-transform: uppercase;
    color: #101010;
    list-style: none;
    cursor: pointer;
    transition: all 0.5s ease 0s;
}

.nav ul li:hover,
.nav ul li:active{
    transform: scale(1.2);
}

.nav span{
    padding-left: 1%;
    padding-right: 1%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    background: #FFFFFF;
    border: 1px solid #F1F1F1;
    border-radius: 10px;    
    flex: 0.250;
    height: 50%;
    /* width: 60%; */
}

.nav span input,
.nav span input::placeholder{
    font-style: normal;
    font-weight: 500;
    font-size: 0.9em;
    line-height: 22px;
    border: none;
    outline: none;    
    letter-spacing: 0.01em;
    color: #999999;
    width: 90%;
}

.nav button{
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 12px 24px; */
    outline: none;
    width: 100%;
    /* height: 3em; */
    height: 50%;
    border: none;
    background: #DDF247;
    border-radius: 8px;
    flex: 0.15;
    font-style: normal;
    font-weight: 600;
    font-size: 1.15em;
    text-align: center;
    letter-spacing: 0.01em;
    color: #101010;
    cursor: pointer;
    box-shadow: 2px 2px 4px 0px #00000016;
    transition: all 0.5s ease 0s;
}

.nav button:hover,
.nav button:active{
    transform: scale(1.2);
}

.icon{
    font-size: 1.5em;
    cursor: pointer;
}